@font-face {
  font-family: "Lato";
  src: url('../fonts/Lato-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato-Black";
  src: url('../fonts/Lato-Black.ttf') format('truetype');
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url('../fonts/Lato-Heavy.ttf') format('truetype');
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url('../fonts/Lato-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url('../fonts/Lato-Semibold.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.container {
  width: 360px;
  margin: 0 auto;
}
.main-header {
  background-color: #0a0a0a;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 18px 20px;
}
.main-nav__link--logo {
  text-decoration: none;
}
.content {
  width: 360px;
  margin: 0 auto;
  background-image: url("../img/main-photo.png");
  min-height: 586px;
}
.content__wrapper {
  width: 230px;
  margin-left: 20px;
}
.content__title {
  font-family: "Lato-Black";
  font-size: 24px;
  font-weight: 400;
  line-height: 32.58px;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  padding-top: 153px;
  margin-bottom: 35px;
  letter-spacing: 0.93px;
  color: #fcfbfb;
}
.content__type {
  position: relative;
  font-size: 14px;
  font-weight: 400;
  line-height: 21.27px;
  letter-spacing: 0.83px;
  color: #fcfbfb;
}
.content__type:after {
  position: absolute;
  bottom: -12px;
  left: -20px;
  content: "";
  width: 200px;
  height: 1px;
  background-color: #ffb600;
}
.content__description {
  position: relative;
  color: #fcfbfb;
  width: 155px;
  font-size: 14px;
}
.content__description:after {
  position: absolute;
  bottom: -47px;
  left: 0;
  content: "";
  width: 12px;
  height: 24px;
  background-image: url("../img/arrow.png");
  background-repeat: no-repeat;
}
.presentation {
  width: 360px;
  margin: 0 auto;
  background-color: #1c1b19;
  margin-top: -52px;
}
.presentation__wrapper {
  position: relative;
  width: 300px;
  min-height: 170px;
  margin: 0 auto;
  padding: 47px 0 30px;
}
.presentation__slogan--wrapper {
  width: 105px;
  margin: 0 auto;
  text-align: right;
}
.presentation__slogan {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 156px;
  min-height: 196px;
  font-family: "Lato-Black";
  color: #000000;
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.64px;
  margin-top: -95px;
  margin-left: -10px;
  background-color: #ffb600;
  z-index: 100;
}
.presentation__slogan--time {
  color: #000000;
  font-family: "Lato - Semibold";
  font-size: 12px;
  font-weight: 400;
  line-height: 15.05px;
  letter-spacing: 1px;
}
.presentation__video {
  position: relative;
  background-color: white;
  z-index: 1000;
}
.advantages {
  width: 360px;
  margin: 0 auto;
  /*background-image: url(../img/small-photo.png);
  background-size: 360px 89px;
  background-repeat: no-repeat;
  background-position: ;*/
}
.advantages__wrapper {
  width: 250px;
  margin: 0 auto;
}
.advantages__title {
  position: relative;
  color: #010101;
  font-size: 14px;
  font-weight: 400;
  line-height: 27.04px;
  letter-spacing: 0.65px;
  text-align: right;
}
.advantages__title:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 2px;
  height: 100%;
  background-color: #ffb600;
}
.advantages__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
}
.advantages__item-numeration {
  position: relative;
  content: "";
  width: 72px;
  height: 72px;
  background-color: #fee4a4;
  border-radius: 50%;
}
.advantages__item-numeration--credit:after {
  position: absolute;
  top: 27px;
  right: 10px;
  content: "";
  background-image: url(../img/credit-card.svg);
  width: 31px;
  height: 31px;
}
.advantages__item-numeration--house:after {
  position: absolute;
  top: 27px;
  right: 10px;
  content: "";
  background-image: url(../img/house.svg);
  width: 31px;
  height: 31px;
}
.advantages__item-numeration--sketch:after {
  position: absolute;
  top: 27px;
  right: 10px;
  content: "";
  background-image: url(../img/sketch.svg);
  width: 31px;
  height: 31px;
}
.advantages__item-numeration--key:after {
  position: absolute;
  top: 27px;
  right: 10px;
  content: "";
  background-image: url(../img/keys.svg);
  width: 30px;
  height: 30px;
}
.advantages__item-numeration--deposit:after {
  position: absolute;
  top: 27px;
  right: 10px;
  content: "";
  background-image: url(../img/deposit.svg);
  width: 30px;
  height: 30px;
}
.advantages__item-numeration--information:after {
  position: absolute;
  top: 27px;
  right: 10px;
  content: "";
  background-image: url(../img/info.svg);
  width: 30px;
  height: 30px;
}
.advantages__item-numeration b {
  position: absolute;
  top: 58px;
  left: 0;
  color: #aba69b;
  font-size: 24px;
  font-weight: 400;
  line-height: 12.19px;
}
.advantages__item {
  width: 93px;
}
.advantages__item-description {
  width: 93px;
  font-size: 10px;
  font-weight: 400;
  line-height: 12.19px;
  margin-bottom: 28px;
}
.audience {
  width: 360px;
  margin: 0 auto;
}
.audience__title {
  position: relative;
  color: #010101;
  font-size: 14px;
  font-weight: 400;
  line-height: 27.04px;
  letter-spacing: 0.65px;
  text-align: right;
  margin: 0;
  padding: 0;
  padding: 0 82px;
}
.audience__title:before {
  position: absolute;
  top: 0;
  left: 82px;
  content: "";
  width: 2px;
  height: 100%;
  background-color: #ffb600;
}
.audience__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.audience__item {
  width: 210px;
}
.audience__item-container {
  width: 211px;
  background-color: #aba69b;
}
.audience__item b {
  color: #0a0a0a;
  font-size: 10px;
  font-weight: 700;
  line-height: 12px;
  letter-spacing: 0.95px;
}
.audience__item-description {
  color: #fffefe;
  font-family: "Lato";
  font-size: 10px;
  font-weight: 700;
  /* Text style for "Бесплатное" */
  line-height: 11px;
  /* Text style for "Своевремен" */
  line-height: 18px;
  /* Text style for "Петербурга" */
  line-height: 8.43px;
}
