  font-display: swap;
}

@font-face {
  font-family: "Lato";
  src: url('../fonts/Lato-Bold.ttf')  format('truetype');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Lato";
  src: url('../fonts/Lato-Semibold.ttf')  format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
